import { useRef } from 'react';
import { ImCheckboxUnchecked } from "@react-icons/all-files/im/ImCheckboxUnchecked";
import { FaCheckSquare } from "@react-icons/all-files/fa/FaCheckSquare";
import { AiFillEdit } from "@react-icons/all-files/ai/AiFillEdit";
import { AiFillSave } from "@react-icons/all-files/ai/AiFillSave";
import { BsArrow90DegDown } from "@react-icons/all-files/bs/BsArrow90DegDown";
import { BsArrowReturnRight } from "@react-icons/all-files/bs/BsArrowReturnRight";

function Todo({ globalFunctions, todoDetails: { id, name, description, startDate, endDate, completed, editMode, viewState } }) {

    const startDateEl = useRef("");
    const endDateEl = useRef("");

    const todaysDate = new Date().toISOString().split("T")[0];
    const chosenMaxDate = "2030-01-01";
    const startMinDate = todaysDate;
    const startMaxDate = chosenMaxDate;
    const endMinDate = todaysDate;
    const endMaxDate = chosenMaxDate;

    const handleCheckboxClick = () => {
        if (!viewState) return; // dont toggle if it is in "frozen" mode
        globalFunctions.app.handleToggleTodo(id);
    }

    const handleEditClick = () => {
        if (!viewState) return;
        globalFunctions.app.handleToggleEditTodo(id);
    }

    const handleModify = (obj, elType = "") => {
        switch (elType) {
            // ensures start date and end date dont overlap in the wrong direction
            case "startDateEl":
                if (obj.startDate > endDate) obj = { ...obj, endDate: obj.startDate };
                break;
            case "endDateEl":
                if (obj.endDate < startDate) obj = { ...obj, startDate: obj.endDate };
                break;
            default: break;
        }
        globalFunctions.app.handleModifyTodo(id, obj);
    }

    return (
        <div className={`todo ${editMode ? 'input' : ''} ${viewState ? 'view' : 'not-view'}`} >
            <div className="todo-content">
                <div>
                    <input type="text" value={name} readOnly={!editMode} onChange={(e) => { handleModify({ name: e.target.value }) }} />
                </div>
                <div>
                    <textarea type="textarea" value={description} readOnly={!editMode} onChange={(e) => { handleModify({ description: e.target.value }) }} />
                </div>
                <div className="startDateContainer">
                    <BsArrow90DegDown />
                    <input type="date" ref={startDateEl} min={startMinDate} max={startMaxDate} value={startDate} readOnly={!editMode} onChange={(e) => { handleModify({ startDate: e.target.value }, "startDateEl") }} />
                </div>
                <div className="endDateContainer">
                    <BsArrowReturnRight />
                    <input type="date" ref={endDateEl} min={endMinDate} max={endMaxDate} value={endDate} readOnly={!editMode} onChange={(e) => { handleModify({ endDate: e.target.value }, "endDateEl") }} />
                </div>
            </div>
            <div className="edit-todo-control" >
                <div onClick={handleCheckboxClick} className={`toggle-checked-btn ${!completed && 'active'}`} >
                    {!completed && <ImCheckboxUnchecked />}
                    {completed && <FaCheckSquare />}
                </div>
                <div onClick={handleEditClick} className={`toggle-edit-btn ${editMode && 'active'}`} >
                    {!editMode && <AiFillEdit />}
                    {editMode && <AiFillSave />}
                </div>
            </div>
        </div>
    );

}

export default Todo;